.container {
  margin: 20px auto 0;
  width: 96%;
}
@media screen and (min-width: 720px) {
  .container {
    width: 90%;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    width: 70%;
  }
}
.input {
  font-size: 3em;
  display: block;
  border: 1px solid #CCC;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  text-align: center;
  width: 100%;
}
.input:focus {
  outline: none;
}
.inOutContainer {
  display: flex;
  flex-direction: column;
}

@media screen and (min-width: 720px) {
  .inOutContainer {
    flex-direction: row;
  }
}
.inputContainer {
  flex: 0 0 320px;
}
.outputContainer {
  flex: 1 1 auto;
}
.inputList {
  list-style: none;
  padding: 0;
}
.inputChoice {
  cursor: pointer;
  margin: 4px;
  padding: 8px;
  border: 1px solid #CCC;
  word-break: break-all;
}
.inputChoice:hover {
  background: rgb(255, 255, 175);
}
.selectedInput {
  font-weight: bold;
  background: #ffff91;
  border-color: #fcfc44;
}
.selectedInput.invalidInput,
.invalidInput,
.invalidInput:hover {
  cursor: not-allowed;
  background: #ffc0c0;
  border-color: #803030;
}
.selectedInput {
  font-weight: bold;
  background: #ffff91;
  border-color: #fcfc44;
}
.inputChoice p {
  margin: 0;
  color: #999;
}
.output {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
}
.output li {
  font-size: 3em;
  border: 1px solid #CCC;
  text-align: center;
}
.output div {
  word-break: break-word;
}
.output p {
  margin: 0;
}
.label {
  font-size: 16px;
  color: #666;
  display: block;
}
.labelName {
  font-size: 8pt;
  color: #666;
  display: block;
}
.char {
  display: inline-block;
  border: 1px solid #d0d0d0;
  font-size: 3em;
  width: 2em;
  text-align: center;
}
.char p {
  margin: 0;
}
.byte {
  display: inline-block;
  border: 1px solid #d0d0d0;
  font-family: 'Courier New', Courier, monospace;
}
.byte > span {
  border-right: 1px dashed #d0d0d0;
}
.byte > span:last-child {
  border-right: none;
}
.binaryByte {
  font-size: 1rem;
}
.bytePrefix {
  color: #999;
}
.byteData {
  font-weight: bold;
}
.switchInput {
  border: 0;
  background: none;
  font-size: 1rem;
  line-height: 1rem;
}
.codePointList {
  display: flex;
  flex-wrap: wrap;
}